body {
  color: #fff;
  background-color: #000;
  justify-content: center;
  display: flex;
}

h1 {
  text-align: center;
}

.task-card {
  border: 2px solid #000;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.fieldset-wrapper {
  justify-content: center;
  margin: 2vh;
  display: flex;
}

fieldset {
  gap: 3vw;
  width: 50vw;
  display: flex;
}

.task-wrapper {
  gap: 5vw;
  display: flex;
}

.task-container {
  text-align: center;
  border-radius: 2vh;
  flex-direction: column;
  justify-content: flex-start;
  width: 20vw;
  height: 50vh;
  margin-top: 4vh;
  display: flex;
  overflow-y: auto;
}

.task-container.new {
  background-color: #00f;
}

.task-container.in-progress {
  background-color: orange;
}

.task-container.finished {
  background-color: green;
}

.filters {
  justify-content: center;
  gap: 1rem;
  margin-bottom: 1rem;
  display: flex;
}

.filters label {
  flex-direction: column;
  max-width: 5rem;
  font-weight: 500;
  display: flex;
}

.filters select {
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 4px 8px;
}
